*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Header component */
.header{
  width: 100%;
  color: #fff;
  background-color: #f5ba13;
  line-height: 100px;
  justify-content: start;
  align-items: center;
  display: flex;
  padding-left: 50px;
  box-shadow: 5px 3px 15px -5px rgba(0,0,0,0.5);
}
.header h1{
  margin-left: 5px;
  font-size: 40px;
}
 .header .g{
  color: rgb(15, 212, 226) ;
}
.header .o{
  color: rgb(255, 94, 0) ;
}
.header .oo{
  color: rgb(255, 251, 0) ;
}
.header .l{
  color: rgb(48, 226, 72) ;
}
.header .keep{
  border-bottom: 5px solid black;
}


/* CreateNote Component */
.main_note{
  width: 500px;
  padding-left: 15px 10px 5px 10px;
  margin: 20px auto;
}
.my_notes h1{
  color:#34495e;
}
.my_notes p{
  color: gray;
}
form{
  min-height: 50px;
  color: #ecf0f1;
  padding: 15px 10px 5px 10px;
  box-shadow: 3px 5px 15px -5px rgba(0,0,0,0.5);
  border-radius: 10px;
  position: relative;
}
input{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1.5rem;
  margin: 10px 0;
  font-weight: bold;
}
textarea{
  width: 100%;
  line-height: 30px;
  background: transparent;
  border: none;
  outline: none;
  font-size: 1rem;
  margin: 10px 0;
}
.MuiButton-root{
  width: 30px;
  height: 50px;
  position: absolute;
  bottom: -28px;
  left: 400px;
  cursor: pointer;
  background-color: #fff !important;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.5);
  border-radius: 50% !important;
}
.MuiButton-root .plus_sign{
  font-size: 45px !important;
  font-weight: 900;
  color: #f4b400;
  cursor: pointer;
  z-index: 222;
  background: transparent;
}
.MuiButton-root:hover{
  background: #f4b400 !important;
}
.MuiButton-root:hover .plus_sign{
  color: #fff;
  background: transparent;
}


/* Note component */
.note{
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 2px 5px #ccc;
  padding: 10px;
  width: 240px;
  margin: 16px;
  float: left;
}
.note h1{
  font-size: 1.1em;
  margin-bottom: 6px;
}
.note p{
  font-size: 1.1em;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}
.note .btn{
  position: relative;
  float: right;
  margin-right: 10px;
  color: #f4b400;
  border: none;
  width: 50px;
  height: 50px;
  background: transparent;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px 15px -10px rgba(0,0,0,0.5);
  border-radius: 50%;
}
.deleteIcon{
  width: 0.7em;
  height: 0.7em;
}
.note .btn:hover{
  background-color: #f5ba13;
}
.note .btn:hover .deleteIcon{
  color: #fff;
}



/* responsive */

@media(max-width:570px){
  .header h1{
    font-size: 30px;
  }
  .main_note{
    width: 400px;
  }
  .MuiButton-root{
    left: 320px;
    bottom: -20px;
  }
  .note{
    width: 220px;
  }
}

@media(max-width:430px){
  .main_note{
    width: 320px;
  }
  .MuiButton-root{
    left: 240px;
    bottom: -15px;
  }
  .note{
    width: 190px;
  }
}

